import React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row, Image } from 'antd';
import css from './Grid.module.css';
import Stars from 'components/Stars/Stars';

export const Grid = ({ arr }) => {
  if (arr.length < 1 || !arr) {
    return;
  }

  return (
    <Row gutter={[16, 16]} className={css.movieRow}>
      {arr.map(({ id, poster_path, name, title, vote_average }) => (
        <Col key={id} xs={12} sm={10} xl={5} className={css.movieCol}>
          <Link to={`/movies/${id}`}>
            {poster_path ? (
              <img
                loading="lazy"
                src={`https://image.tmdb.org/t/p/w500/${poster_path}`}
                alt={title || name}
                className={css.movieImage}
              />
            ) : (
              <Image
                width={'100%'}
                height={'100%'}
                src="error"
                className={css.movieImage}
                fallback="data:image/png;base64,"
              />
            )}
            <div className={css.wrapper}>
              <div className={css.wrapperInfo}>
                <h4 className={css.movieTitle}>{title || name}</h4>
                <Stars
                  rating={vote_average}
                  secondaryColor={'white'}
                  style={css.stars}
                />
              </div>
            </div>
          </Link>
        </Col>
      ))}
    </Row>
  );
};
